<template>
  <v-card flat max-width="100%">
    <v-card-text>
      <v-row align="center" justify="center">
        <v-col>
          <v-card class="mx-auto">
            <v-alert color="primary" dark icon="mdi-emoticon" border="left" prominent>
              <p>想要更改头像？</p>
              <p>
                我们从Gravatar同步您的头像，如果您想更改您的头像：
                <br />
                点击按钮，使用{{ email }}注册Gravatar并上传您的头像
              </p>
              <v-col class="shrink">
                <v-btn @click="go_gravatar()">Go Gravatar</v-btn>
              </v-col>
            </v-alert>
          </v-card>
        </v-col>

        <v-col>
          <v-card class="mx-4 pa-12">
            <v-list-item>
              <v-list-item-content>
                <v-list-item-title>个人信息</v-list-item-title>
              </v-list-item-content>
            </v-list-item>

            <v-list-item two-line>
              <v-list-item-content>
                <v-list-item-title>用户名</v-list-item-title>
                <v-list-item-subtitle>{{ username }}</v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>

            <v-list-item three-line>
              <v-list-item-content>
                <v-list-item-title>邮箱</v-list-item-title>
                <v-list-item-subtitle>{{ email }}</v-list-item-subtitle>
                <v-list-item-subtitle v-if="email_verify == 1">(已验证)</v-list-item-subtitle>
                <v-list-item-subtitle v-else>(未验证)</v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>

            <v-list-item four-line>
              <v-list-item-content>
                <v-list-item-title>用户类型</v-list-item-title>
                <v-list-item-subtitle>{{ usertype }}</v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
          </v-card>
        </v-col>

        <v-col>
          <v-card class="ma-4 pa-12">
            <v-list-item>
              <v-list-item-content>
                <v-list-item-title>其他信息</v-list-item-title>
              </v-list-item-content>
            </v-list-item>

            <v-list-item two-line>
              <v-list-item-content>
                <v-text-field
                  :value="mystate"
                  @change="callOnMyStateChanged"
                  type="text"
                  label="签名"
                ></v-text-field>
              </v-list-item-content>
            </v-list-item>

            <v-list-item three-line>
              <v-list-item-content>
                <v-select
                  @change="callOnSexChanged"
                  :items="sexitems"
                  :value="sex"
                  item-text="label"
                  item-value="value"
                  label="性别"
                ></v-select>
              </v-list-item-content>
            </v-list-item>

            <v-list-item four-line>
              <v-list-item-content>
                <v-list-item-title>
                  <v-btn color="blue" class="white--text" @click="change_profile()">更新</v-btn>
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-card>
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>

<script>
import sexdata from "@/helpers/sex";
import { mapState, mapMutations } from "vuex";
import { ChangeProfile } from "@/request/api";

export default {
  data() {
    return {
      sexitems: sexdata
    };
  },
  computed: {
    ...mapState({
      avatar: state => state.user.avatar,
      username: state => state.user.username,
      email: state => state.user.email,
      email_verify: state => state.user.email_verify,
      usertype: state => state.user.usertype,
      mystate: state => state.user.mystate,
      sex: state => state.user.sex
    })
  },
  methods: {
    //更新个人资料
    change_profile() {
      // if (!this.autograph) {
      //   this.$snackbar.warning("请输入签名内容");
      // } else if (this.autograph == this.mystate) {
      //   this.$snackbar.warning("签名内容一致，无需更改");
      // } else {
      ChangeProfile({
        mystate: this.mystate,
        sex: this.sex
      }).then(res => {
        if (res.code != 0) {
          this.$snackbar.error(res.message);
        } else {
          this.$snackbar.success(res.message);
        }
      });
    },

    callOnMyStateChanged(state) {
      this.updateMyState(state);
    },

    callOnSexChanged(sex) {
      this.updateSex(sex);
    },

    go_gravatar() {
      window.location.href = "https://gravatar.com";
    },

    ...mapMutations(["updateMyState", "updateSex"])
  }
};
</script>
